import React from 'react';
// This assumes background.jpg is located in your src/ folder. 
// If it's in src/assets, change the path to '../assets/background.jpg'
import profileImg from '../profile.png'; 

export default function Navbar() {
  return (
    <nav className="navbar" style={navStyle}>
      <div className="nav-profile" style={profileContainerStyle}>
        <img 
          src={profileImg} 
          alt="Tharindu" 
          style={imageStyle} 
        />
        <h1 className="logo" style={logoStyle}>Tharindu D Rajapaksha</h1>
      </div>
      <div className="nav-links" style={linksContainerStyle}>
        <a href="#projects" style={linkStyle}>Projects</a>
        <a href="#publications" style={linkStyle}>Publications</a>
        <a href="/cv.pdf" target="_blank" rel="noopener noreferrer" style={linkStyle}>CV</a>
      </div>
    </nav>
  );
}

const navStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  padding: '0.8rem 2rem',
  /* Dark blue gradient for glossy effect */
  background: 'linear-gradient(180deg, #1a2a6c 0%, #000046 50%, #1a2a6c 100%)',
  boxShadow: '0 4px 10px rgba(0, 0, 0, 0.3)',
  borderBottom: '1px solid rgba(255, 255, 255, 0.1)',
  position: 'sticky',
  top: 0,
  zIndex: 1000
};

const profileContainerStyle = {
  display: 'flex',
  alignItems: 'center',
  gap: '12px'
};

const imageStyle = {
  width: '42px',
  height: '42px',
  borderRadius: '50%',
  objectFit: 'cover',
  border: '2px solid rgba(255, 255, 255, 0.8)', // White border makes it pop against blue
  boxShadow: '0 0 8px rgba(255, 255, 255, 0.2)'
};

const logoStyle = {
  fontSize: '1.4rem',
  margin: 0,
  fontWeight: 'bold',
  color: '#ffffff', // White text for contrast
  textShadow: '1px 1px 2px rgba(0,0,0,0.5)'
};

const linksContainerStyle = {
  display: 'flex',
  gap: '25px'
};

const linkStyle = {
  textDecoration: 'none',
  color: '#e0e0e0', // Off-white for better readability
  fontWeight: '500',
  fontSize: '1rem',
  transition: 'color 0.3s ease'
};