import React, { useEffect, useMemo, useState } from "react";
import bibtexParse from "bibtex-parse-js";
import { motion } from "framer-motion";

export default function Publications() {
  const [entries, setEntries] = useState([]);
  const [filter, setFilter] = useState("all");

  useEffect(() => {
    fetch("/publications.bib")
      .then((res) => res.text())
      .then((bibtexText) => {
        const parsed = bibtexParse.toJSON(bibtexText);
        setEntries(parsed);
      });
  }, []);

  const publications = useMemo(() => {
    return entries
      .map((e) => ({
        id: e.citationKey,
        title: e.entryTags?.title,
        authors: e.entryTags?.author,
        journal: e.entryTags?.journal || e.entryTags?.booktitle,
        year: e.entryTags?.year,
        doi: e.entryTags?.doi,
        url: e.entryTags?.url,
        type: e.entryType,
      }))
      .filter((p) => filter === "all" || p.type === filter)
      .sort((a, b) => b.year - a.year);
  }, [entries, filter]);

  return (
    <section id="publications" className="p-8 max-w-5xl mx-auto">
      <h2 className="text-3xl font-bold mb-4">Publications</h2>

      {/* Filter */}
      <div className="flex gap-2 mb-6 text-sm">
        {["all", "article", "inproceedings"].map((t) => (
          <button
            key={t}
            onClick={() => setFilter(t)}
            className={`px-3 py-1 rounded-full border ${
              filter === t ? "bg-black text-white" : "bg-white"
            }`}
          >
            {t === "all" ? "All" : t}
          </button>
        ))}
      </div>

      {publications.length === 0 && (
        <p className="text-gray-500">Loading publications</p>
      )}

      <div className="space-y-5">
        {publications.map((p, i) => (
          <motion.div
            key={p.id || i}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.4 }}
            className="rounded-2xl p-5 shadow-sm border bg-white"
          >
            <h3 className="text-lg font-semibold mb-1">{p.title}</h3>
            <p className="text-sm text-gray-700 mb-1">{p.authors}</p>
            <p className="text-sm italic text-gray-600">
              {p.journal} ({p.year})
            </p>

            {/* Links */}
            <div className="flex gap-4 mt-2 text-sm">
              {p.doi && (
                <a
                  href={`https://doi.org/${p.doi}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="underline"
                >
                  DOI
                </a>
              )}
              {p.url && (
                <a
                  href={p.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="underline"
                >
                  PDF
                </a>
              )}
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
